import { Injectable, inject } from '@angular/core';
import { HttpClient, HttpParams } from '@angular/common/http';
import { Observable } from 'rxjs';
import { map } from 'rxjs/operators';
import { AppReview } from '../models/app-store.models';
import {
  AppReviewApiResponse,
  AppReviewListApiResponse,
  CreateAppReviewRequest,
  ReviewListQueryParams,
} from '../models/api/app-store-api.models';
import { mapAppReviewFromApi } from '../models/api/app-store-api.mappers';
import { ApiUrlService } from '../../../core/services/api-url.service';

@Injectable({
  providedIn: 'root',
})
export class ReviewService {
  private readonly http = inject(HttpClient);
  private readonly apiUrlService = inject(ApiUrlService);
  private readonly baseUrl: string;

  constructor() {
    this.baseUrl = `${this.apiUrlService.getCurrentApiUrl()}/appstore`;
  }

  /**
   * Get reviews for a specific app
   * @param appId The app ID
   * @param params Optional query parameters for filtering
   */
  getReviewsForApp(
    appId: string,
    params?: ReviewListQueryParams,
  ): Observable<AppReview[]> {
    let httpParams = new HttpParams();

    if (params) {
      if (params.owner !== undefined) {
        httpParams = httpParams.set('owner', params.owner);
      }
      if (params.offset !== undefined) {
        httpParams = httpParams.set('offset', params.offset.toString());
      }
      if (params.limit !== undefined) {
        httpParams = httpParams.set('limit', params.limit.toString());
      }
      if (params.sort !== undefined) {
        httpParams = httpParams.set('sort', params.sort);
      }
      if (params.filter !== undefined) {
        httpParams = httpParams.set('filter', params.filter);
      }
    }

    return this.http
      .get<AppReviewListApiResponse>(`${this.baseUrl}/apps/${appId}/reviews`, {
        params: httpParams,
      })
      .pipe(map((response) => response.items.map(mapAppReviewFromApi)));
  }

  /**
   * Add a review for an app
   * @param appId The app ID
   * @param review Review data
   */
  addReview(appId: string, review: Partial<AppReview>): Observable<AppReview> {
    const request: CreateAppReviewRequest = {
      appId,
      ownerId: review.userId ?? '',
      userName: review.userName ?? '',
      organizationName: review.organizationName ?? '',
      rating: review.rating ?? 0,
      title: review.title ?? '',
      comment: review.comment ?? '',
    };

    return this.http
      .post<AppReviewApiResponse>(
        `${this.baseUrl}/apps/${appId}/reviews`,
        request,
      )
      .pipe(map(mapAppReviewFromApi));
  }
}
